import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页显示  true|false
			cartList: [],
			edit: false,
			del: false,
			title:'收藏',
			btnCheck:'全选',
			btnEdit:'编辑'
		};
	},
	onLoad() {
		this.loadData();
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData() {
			let list = await this.$api.json('cartList');
			let cartList = list.map(item => {
				item.checked = true;
				return item;
			});
			this.cartList = cartList;
			this.calcTotal(); //计算总价
			
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		editList(e){
			if(this.btnEdit==='编辑'){
				this.edit=true;
				this.del=true;
				this.btnEdit='完成';
			}else{
			this.btnEdit='编辑';
			this.edit=false;
			this.del=false;
			}
		},
		back(){
			uni.navigateBack();
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.id;

			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//删除选中
		clearCart() {
			uni.showModal({
				content: '删除选中购物车？',
				success: (e) => {
					if (e.confirm) {
						let list = this.cartList;
						let selectedlist = [];
						list.forEach(item => {
							if (item.checked === false) {
								selectedlist.push(item);
							}
						})
						this.cartList = selectedlist;
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.number;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder() {
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item => {
				if (item.checked) {
					goodsData.push({
						attr_val: item.attr_val,
						number: item.number
					})
				}
			})

			uni.navigateTo({
				url: `/pages/order/createOrder?data=${JSON.stringify({
					goodsData: goodsData
				})}`
			})
			this.$api.msg('跳转下一页 sendData');
		}
	},

}
